import Card from 'components/Card/Card';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Scrollbar, EffectCoverflow } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/scrollbar';
import 'swiper/css/effect-coverflow';
import {
  StyledCardSection,
  StyledGallerySection,
  StyledHeroSection,
  StyledWelcomeSection,
} from './Home.styled';
import Gallery from 'components/Gallery/Gallery';
import { themeContext } from 'context/ThemeContext';
import { useContext, useEffect, useRef } from 'react';
import * as prismicH from '@prismicio/helpers';
import { useAllPrismicDocumentsByType } from '@prismicio/react';
import { useLocation } from 'react-router-dom';

const Home = () => {
  const { theme } = useContext(themeContext);
  const [documents] = useAllPrismicDocumentsByType('mountain-routes');
  const galleryRef = useRef();
  const routesRef = useRef();
  const location = useLocation();

  useEffect(() => {
    if (!location.hash) return;
    if (location.hash === '#gallery') {
      galleryRef.current.scrollIntoView({
        behavior: 'smooth',
        alignToTop: true,
      });
    }
    if (location.hash === '#routes') {
      routesRef.current.scrollIntoView({
        behavior: 'smooth',
        alignToTop: true,
      });
    }
  }, [location.hash]);

  const cards = documents?.map(doc => doc.data.mountain_card?.[0]) ?? [];

  return (
    <div>
      <StyledHeroSection $theme={theme}>
        <h1 className="animate__animated animate__fadeInDown">
             .   .
        </h1>
      </StyledHeroSection>
      <StyledWelcomeSection title="    !">
        <p className="text">
              ,    ,  
                .     
             ,      
          .
          <br />   " "    
               ,   
               .    
             ,      .
          <br />
                  ,   
            .     
          ,   ,       ,  
          .
          <br />        
           ,        
          ,       .  
           ,         
          .         
           !
        </p>
      </StyledWelcomeSection>
      <StyledGallerySection ref={galleryRef} title="" id="gallery">
        <p className="text">
                ,   
                 .   
            ,   ,     
            ,     .
                  .
               ,   
            ,   ,   '
          .
        </p>
        <Gallery />
      </StyledGallerySection>
      <StyledCardSection ref={routesRef} title="" id="routes">
        <p className="text">
              !      
                 ,
              .    
           !
        </p>
        <Swiper
          className="swiper"
          modules={[Scrollbar, EffectCoverflow]}
          effect="coverflow"
          initialSlide={2}
          loop={true}
          spaceBetween={20}
          slidesPerView={3}
          scrollbar={{ draggable: true }}
          breakpoints={{
            320: {
              slidesPerView: 1,
              spaceBetween: 20,
              coverflowEffect: {
                rotate: 40,
                stretch: 30,
                modifier: 2,
                slideShadows: false,
              },
            },
            475: {
              slidesPerView: 1,
              spaceBetween: 30,
            },
            768: {
              slidesPerView: 2,
              spaceBetween: 50,
              coverflowEffect: {
                rotate: 20,
                stretch: 30,
                depth: 50,
                modifier: 2,
                slideShadows: false,
              },
            },
            1200: {
              slidesPerView: 3,
              coverflowEffect: {
                rotate: 20,
                stretch: 30,
                depth: 90,
                modifier: 2,
                slideShadows: true,
              },
            },
          }}
          coverflowEffect={{
            rotate: 20,
            stretch: 30,
            depth: 90,
            modifier: 2,
            slideShadows: true,
          }}
        >
          {cards.map(mount => {
            const title = prismicH.asText(mount.card_title);
            const id = prismicH.asText(mount.card_id);
            const thumb = mount.card_img.url;
            const description = prismicH.asText(mount.card_description);
            return (
              <SwiperSlide key={title}>
                <Card
                  id={id}
                  title={title}
                  thumbs={thumb}
                  description={description}
                />
              </SwiperSlide>
            );
          })}
        </Swiper>
      </StyledCardSection>
    </div>
  );
};

export default Home;
